// TechBubble Component

import React from 'react'
import './tech-bubble.scss'
import {Link} from 'react-router-dom'


function TechBubble (props) {
  let image = require(`../../assets/images/techs/${props.img}`);
  
  return (
    <figure className="techBubble">
      <Link to={`/technologies/${props.url}`} aria-label={props.name}>
        <img src={image.default} alt={props.name} loading="lazy" />
      </Link>
      <figcaption>
        {props.name}
      </figcaption>
    </figure>
  )
}

export default TechBubble
