import React from 'react'
import './cert-bubble.scss'
import {Link} from 'react-router-dom'

function CertBubble(props) {

  let image = require(`../../assets/images/techs/${props.img}`);
  
  return (
      <figure className="certBubble">
      <Link to={`/training/${props.uri}`} aria-label={props.name}>
        <img src={image.default} alt={props.name} loading="lazy" />
      </Link>
      <figcaption>
        {props.name}
      </figcaption>
    </figure>
  )
}

export default CertBubble
